.mainSecondary{

    height: auto;
    width: 100%;
    grid-area: 1 / 1 / span 1/ span 12;
    transform-origin: 0% 0%;
    transform: scaleX(1);
    animation-name: mymove;
    animation-duration: 1s;
}
.sobra{
    display: none;
}
@keyframes opacitybottom1 {
    from {opacity: 0;}
    to {opacity: 1}
  }


@keyframes translatebottom2 {
    from {transform: translate(100%,-32px);}
    to {transform: translate(-32px,-32px);}
  }

@media only screen and (max-width: 768px) {
    .sobra{
        display: initial;
        position: fixed;
        background-color: red;

    }
    .bottom1 {
        position: absolute;
        transform-origin: 0% 0%;
        transform: translate(-32px,-32px);
        width: calc(100% + 64px);
        height: 100%;
        z-index: -1;
        background-color: black;
        animation: opacitybottom1 200ms;
    
    }
    .bottom2 {
        position: absolute;
        transform-origin: 0% 0%;
        transform: translate(-32px,-32px);
        width: calc(100% + 64px);
        height: 100%;
        z-index: -1;
        background-color: white;
        animation-delay: 200ms;
        animation: translatebottom2 1s;
    
    }
    .wrapperSecondaryNav{
        position: absolute;
        top: 88px;
        z-index: 0;
        padding: 0px 32px;
        border-style: none;
        border-width: 0px;
    }
    
    .mainSecondary{
        animation-name: none;
        padding: 32px 0px;
        height: auto;
        grid-area: 1 / 4 / span 1/ span 3;
    }
    
}
@media only screen and (max-width: 360px) {
    .wrapperSecondaryNav{
        top: 180px;   
      
    }
    .mainSecondary{
        height: auto;
        grid-area: 1 / 1 / span 1/ span 4;
    }
    
}
@keyframes mymove {
    from {opacity: 0%;}
    to {opacity: 1}
  }