
.burguerIcon
{
    display: none;

}
@keyframes opacitybottom1 {
    from {opacity: 0;}
    to {opacity: 1}
  }

@media only screen and (max-width: 768px) {
    .burguerIcon
    {
        animation-name: opacitybottom1;
        animation-duration: 1s;
        opacity: 1;
        display: initial;
        cursor: pointer;
    }

}


